import React from "react";
import { Container, Typography, Paper, Button, Box } from "@mui/material";
import Link from "next/link";

export default function Home() {
  return (
    <Container maxWidth="lg" sx={{ py: 8, pl: { xs: 0, md: 10 }, textAlign: "center" }}>
      <Paper elevation={4} sx={{ p: 5, borderRadius: 3, bgcolor: "background.paper", boxShadow: 3 }}>
        <Typography variant="h3" color="primary" fontWeight="bold" gutterBottom>
          Welcome to the Real-Time Dashboard
        </Typography>
        <Typography variant="h6" color="textSecondary" paragraph>
          Monitor live air quality data with real-time updates.
        </Typography>

        <Box mt={3}>
          <Link href="/dashboard" passHref>
            <Button variant="contained" color="primary" size="large" sx={{ borderRadius: 2 }}>
              Go to Dashboard
            </Button>
          </Link>
        </Box>
      </Paper>
    </Container>
  );
}
